import React, { useState } from "react";

function UserList(props) {
    const [userList, setUserList] = useState([{ userName: "lol" }]);

    return (
        <ul>
            {userList.map(user => (
                <li key={user.userName}>{user.id}</li>
            ))}
        </ul>
    );
}

export default UserList;
